export default {
    name: "CommonMixin",
    data(){
        return{
            multiSelectArray: [],
            rows: [],
            config: {
                search: false,
                searchStr: null,
                selectedVal: null
            }
        }
    },
    computed: {
        restData(){
            let list = this.rows;
            if(this.config.search){
                list = this.rows.filter( data => {
                    if(!this.config.selectedVal || !data[this.config.selectedVal]){
                        return false;
                    }
                    return data[this.config.selectedVal].toLowerCase().includes(this.config.searchStr.toLowerCase())
                });
            }
            return list;
        }
    },
    methods: {
        setMultiselect(selected){
            this.multiSelectArray = selected
        },
        setSearch(searchStr, selectedVal){
            this.config = {...{ searchStr, selectedVal, search: true }};
        }
    },
    mounted() {

    }
}