<template>
  <div class="driver-profile" style="padding: 20px; display: flex; flex-direction: column; overflow: scroll; height: calc(100% - 140px)">
    <div style="display: flex;">
      <div>
        <img v-if="addDriverForm.photo" height="90" width="90" style="border-radius: 50%;" :src="addDriverForm.photo" />
        <img v-else height="90" width="90" style="border-radius: 50%;" :src="getDefaultImage()" />
      </div>
      <div style="margin-left: 20px; display: flex; align-items: flex-start; flex-direction: column;margin-top: 5px;">
        <div style="font-weight: 700; font-size: 26px;text-align: left;">{{ getDriverName }}</div>
        <div style="font-size: 18px;text-align: left;font-weight: 500;letter-spacing: 1.1px;">{{ addDriverForm.emp_id || "-" }}</div>
      </div>
    </div>
    <div style="display: flex; flex-direction: row; margin-top: 10px; margin-left: 10px;">
      <div class="driver-profile-form-group" style="margin-left: 0 !important;">
        <div class="driver-profile-form">
          <div>Number</div>
          <div class="form-bg" style="text-align: left">{{ addDriverForm.phoneNumber || "-" }}</div>
        </div>

        <div class="driver-profile-form">
          <div>DOB</div>
          <div class="form-bg" style="text-align: left">{{ addDriverForm.dob || "-" }}</div>
        </div>
      </div>

      <div class="driver-profile-form-group">
        <div class="driver-profile-form">
          <div>Date of joining</div>
          <div class="form-bg" style="text-align: left">{{ addDriverForm.dateOfJoining || "-" }}</div>
        </div>

        <div class="driver-profile-form">
          <div>Gender</div>
          <div class="form-bg" style="text-align: left">{{ addDriverForm.gender || "-" }}</div>
        </div>
      </div>

      <div class="driver-profile-form-group">
        <div class="driver-profile-form">
          <div>Emergency number</div>
          <div class="form-bg" style="text-align: left">{{ addDriverForm.emergencyNumber || "-" }}</div>
        </div>

        <div class="driver-profile-form">
          <div>Blood group</div>
          <div class="form-bg" style="text-align: left">{{ addDriverForm.bloodGroup || "-" }}</div>
        </div>
      </div>

      <div class="driver-profile-form-group driver-profile-form" style="flex: 1;">
        <div>Address</div>
        <div class="form-bg">
          <div style="text-align: left; height: 135px; font-weight: 500 !important;">{{ addDriverForm.address || "-" }}</div>
        </div>
      </div>
    </div>
    <div style="margin-left: 10px; margin-top: 20px;">
      <div>
        <div class="driver-profile-form">
          <div>Aadhaar</div>
          <div style="display: flex;">
            <div>
              <el-image v-if="addDriverForm.acFront" style="width: 300px; height: 200px" :src="addDriverForm.acFront" :preview-src-list="[addDriverForm.acFront]"></el-image>
              <img v-else height="100" width="200" :src="'x'" alt="No aadhaar front" />
            </div>
            <div style="margin-left: 20px;">
              <el-image v-if="addDriverForm.acBack" style="width: 300px; height: 200px" :src="addDriverForm.acBack" :preview-src-list="[addDriverForm.acBack]"></el-image>
              <img v-else height="100" width="200" :src="'x'" alt="No aadhaar back" />
            </div>
          </div>
        </div>

        <div class="driver-profile-form">
          <div>License</div>
          <div style="display: flex;">
            <div>
              <el-image v-if="addDriverForm.licenseFront" style="width: 300px; height: 200px" :src="addDriverForm.licenseFront" :preview-src-list="[addDriverForm.licenseFront]"></el-image>
              <img v-else height="100" width="200" :src="'x'" alt="No license front" />
            </div>
            <div style="margin-left: 20px;">
              <el-image v-if="addDriverForm.licenseBack" style="width: 300px; height: 200px" :src="addDriverForm.licenseBack" :preview-src-list="[addDriverForm.licenseBack]"></el-image>
              <img v-else height="100" width="200" :src="'x'" alt="No license front" />
            </div>
          </div>
        </div>
      </div>
      <div></div>
    </div>
  </div>
</template>

<script>
import { initializeApp } from "firebase/app";
import { getStorage, getDownloadURL, ref } from "firebase/storage";

const firebaseConfig = {
  apiKey: "AIzaSyAKMfcrJJE2_25IWpn2oKE11UxNV2SwlXc",
  authDomain: "leggo-1429.firebaseapp.com",
  projectId: "leggo-1429",
  storageBucket: "leggo-1429.appspot.com",
  messagingSenderId: "163945921178",
  appId: "1:163945921178:web:a5ee98ec8c3e1a7f0bfd23",
  measurementId: "G-F00TMQXLRE"
};

const app = initializeApp(firebaseConfig);
const storage = getStorage(app);

export default {
  name: "DriverProfile",
  data(){
    return{
      addDriverForm: {
        photo: null,
        name: null,
        emergencyNumber: null,
        empId: null,
        gender: null,
        dob: null,
        address: null,
        licenseFront: null,
        licenseBack: null,
        acFront: null,
        acBack: null,
        bloodGroup: null,
        badgeStatus: false,
        badgeExpiry: null,
        phoneNumber: null,
        dateOfJoining: null
      }
    }
  },
  computed: {
    srcList(){
      return [
          this.addDriverForm.photo,
          this.addDriverForm.acFront,
          this.addDriverForm.acBack,
          this.addDriverForm.licenseFront,
          this.addDriverForm.licenseBack
      ]
    },
    getDriverId(){
      return this.$route.params.driverId || null;
    },
    getDriverName(){
      let name = "";
      if(this.addDriverForm.name){
        name = this.addDriverForm.name;
      }
      return name === "" ? "-" : name;
    }
  },
  methods: {
    getDefaultImage(){
      return require("/src/assets/emptyProfile.png")
    },
    getProfileFile(){
      if(this.addDriverForm.phoneNumber) {
        getDownloadURL(ref(storage, `file/profile/${this.addDriverForm.phoneNumber}-profile`)).then(url => this.addDriverForm.photo = url);

        getDownloadURL(ref(storage, `file/aadhaar/${this.addDriverForm.phoneNumber}-aadhaar-front`)).then(url => this.addDriverForm.acFront = url);

        getDownloadURL(ref(storage, `file/aadhaar/${this.addDriverForm.phoneNumber}-aadhaar-back`)).then(url => this.addDriverForm.acBack = url);

        getDownloadURL(ref(storage, `file/license/${this.addDriverForm.phoneNumber}-license-front`)).then(url => this.addDriverForm.licenseFront = url);

        getDownloadURL(ref(storage, `file/license/${this.addDriverForm.phoneNumber}-license-back`)).then(url => this.addDriverForm.licenseBack = url);
      }
    }
  },
  mounted() {
      const url = `driver/view?driverId=${this.getDriverId}`;
      fetch(url).then(response => response.json())
          .then(res => {
            this.addDriverForm.name = res.driverFisrtName;
            this.addDriverForm.dob = res.dob;
            this.addDriverForm.phoneNumber = res.phoneNo;
            this.addDriverForm.emp_id = res.emp;
            this.addDriverForm.dateOfJoining = res.date_of_joining;
            this.addDriverForm.gender = res.gender;
            this.addDriverForm.address = res.address;
            this.addDriverForm.bloodGroup = res.blood_group;
            this.addDriverForm.emergencyNumber = res.emergency_number;

            this.getProfileFile();
          });
  }
}
</script>

