<template>
  <div class="json-widget-box-container">
    <div
        :class="
        'json-widget-box ' + this.conf.style.container.color + '-background'
      "
        ref="container"
    >
      <div
          :class="
          this.conf.style.title.gradient
            ? 'json-widget-title gradient-text'
            : 'json-widget-title ' + this.conf.style.title.color + '-color'
        "
          :style="
          this.conf.style.title.gradient
            ? { backgroundImage: this.conf.style.title.gradientColor }
            : {}
        "
      >
        {{ title }}
      </div>
      <div
          :class="
          this.conf.style.count.gradient
            ? 'count gradient-text'
            : 'count ' + this.conf.style.count.color + '-color'
        "
          :style="
          this.conf.style.count.gradient
            ? { backgroundImage: this.conf.style.count.gradientColor }
            : {}
        "
      >
        <el-tooltip placement="right">
          <div style="width: max-content">
            {{ extractCount }}
          </div>
          <div slot="content" style="padding: 2px 6px;">{{ parseInt(extractCount).toLocaleString() }}</div>
        </el-tooltip>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "jsonwidget",
  data() {
    return {};
  },
  // mixins : [ VUE_REST_CLIENT ],
  props: {
    title: String,
    conf: Object,
    data: Object,
  },
  computed: {
    extractCount() {
      if (this.data.hasOwnProperty(this.conf.name)) {
        return this.data[this.conf.name]+"";
      } else {
        return "0";
      }
    }
  },
  mounted() {
    if (!this.conf.hasOwnProperty("reload")) {
      this.conf.reload = false;
    }
    if (!this.conf.hasOwnProperty("name")) {
      console.error("Please provide 'name' in conf object");
    }
    if (!this.conf.hasOwnProperty("style")) {
      this.conf.style = {
        container: { color: "blue" },
        iconContainer: { color: "white" },
        title: { color: "white" },
        count: { color: "white" }
      };
    } else {
      if (!this.conf.style.hasOwnProperty("container")) {
        this.conf.style.container = { color: "blue" };
      }
      if (!this.conf.style.hasOwnProperty("iconContainer")) {
        this.conf.style.iconContainer = { color: "white" };
      }
      if (!this.conf.style.hasOwnProperty("title")) {
        this.conf.style.title = { color: "white" };
      }
      if (!this.conf.style.hasOwnProperty("count")) {
        this.conf.style.count = { color: "white" };
      }
    }
  }
};
</script>

