        <div style="margin-bottom: 10px;border-radius: 5px;background: #1ADB8A;color: white; font-weight: 600;text-align: left; padding-left: 10px;height: 30px;padding-top: 10px; border-bottom-left-radius: unset; border-bottom-right-radius: unset;">Driving License</div>
        <div style="padding-left: 10px;">
          <div style="display: flex; padding-right: 10px;">
        <el-form-item label="Driving License Front" prop="licenseFront" required>
            <el-upload
                class="upload-demo"
                drag
                v-model="addDriverForm.licenseFront"
                :auto-upload="false"
                :on-change="(e) => addDriverForm.licenseFront = e.raw"
                :on-remove="(e) => addDriverForm.licenseFront = null"
            >
