<script>
// Import the functions you need from the SDKs you need
import { initializeApp } from "firebase/app";
import { getStorage, uploadBytes, ref } from "firebase/storage";
import { UploadFilled } from '@element-plus/icons-vue';
import {ElMessage} from "element-plus";
export default {
  name: "AddDrivers",
  components: { UploadFilled },
  data(){
    return {
      addDriverForm: {
        photo: null,
        name: null,
        emergencyNumber: null,
        empId: null,
        gender: null,
        dob: null,
        address: null,
        licenseFront: null,
        licenseBack: null,
        acFront: null,
        acBack: null,
        bloodGroup: null,
        badgeStatus: false,
        badgeExpiry: null,
        phoneNumber: null,
        dateOfJoining: null
      }
    }
  },
  computed: {
    isEditDriver(){
      return this.$route.fullPath === "/drivers/edit/" + this.$route.params.driverId;
    },
    getDriverId(){
      return this.$route.params.driverId || null;
    }
  },
  methods: {
    goBack(){
      history.back();
    },
    openDrivers(){
      window.location.href = "#/drivers";
    },
    submitForm(){
      this.$refs.driverForm.validate(
          (valid, fields) => {
            if(valid){
              const firebaseConfig = {
                apiKey: "AIzaSyAKMfcrJJE2_25IWpn2oKE11UxNV2SwlXc",
                authDomain: "leggo-1429.firebaseapp.com",
                projectId: "leggo-1429",
                storageBucket: "leggo-1429.appspot.com",
                messagingSenderId: "163945921178",
                appId: "1:163945921178:web:a5ee98ec8c3e1a7f0bfd23",
                measurementId: "G-F00TMQXLRE"
              };

              const app = initializeApp(firebaseConfig);
              const storage = getStorage(app);

              let data = {
                name: this.addDriverForm.name,
                phone_number: this.addDriverForm.phoneNumber,
                emp_id: this.addDriverForm.emp_id,
                gender: this.addDriverForm.gender,
                date_of_birth: this.addDriverForm.dob,
                address: this.addDriverForm.address,
                date_of_joining: this.addDriverForm.dateOfJoining,
                emergency_number: this.addDriverForm.emergencyNumber,
                blood_group: this.addDriverForm.bloodGroup,
                badge_status: this.addDriverForm.badgeStatus,
                badge_expiry: this.addDriverForm.badgeExpiry
              };

              let promiseArray = [];

              promiseArray.push(fetch("/addDriver", {
                method: "POST",
                headers: {'Content-Type': 'application/x-www-form-urlencoded'},
                body: Object.keys(data).filter(each => {
                  if(data[each] !== null && data[each] !== undefined && data[each] !== ""){
                    return true;
                  }
                }).map(each => each + "=" + encodeURIComponent(data[each])).join("&")
              }));

              promiseArray.push(uploadBytes(ref(storage, `file/profile/${this.addDriverForm.phoneNumber}-profile`), this.addDriverForm.photo));

              promiseArray.push(uploadBytes(ref(storage, `file/aadhaar/${this.addDriverForm.phoneNumber}-aadhaar-front`), this.addDriverForm.acFront));

              promiseArray.push(uploadBytes(ref(storage, `file/aadhaar/${this.addDriverForm.phoneNumber}-aadhaar-back`), this.addDriverForm.acBack));

              promiseArray.push(uploadBytes(ref(storage, `file/license/${this.addDriverForm.phoneNumber}-license-front`), this.addDriverForm.licenseFront));

              promiseArray.push(uploadBytes(ref(storage, `file/license/${this.addDriverForm.phoneNumber}-license-back`), this.addDriverForm.licenseBack));


              Promise.all(promiseArray).then(res => {
                ElMessage({
                  type: 'success',
                  message: 'Successfully added!',
                });
                setTimeout(() => {
                  window.location.href = '#/drivers';
                }, 1000);
              });
            }
            else{
              ElMessage({
                type: 'error',
                message: 'Please fill the mandatory fields',
              });
            }
          }
      );
    },
    resetForm() {
      this.addDriverForm.firstName = null;
      this.addDriverForm.lastName = null;
      this.addDriverForm.dob = null;
      this.addDriverForm.contactNumber = null;
      this.addDriverForm.emp_id = null;
      this.addDriverForm.gender = null;
      this.addDriverForm.address = null;
      this.addDriverForm.city = null;
      this.addDriverForm.state = null;
      this.addDriverForm.fleet = null;
      this.addDriverForm.pinCode = null;

      this.addDriverForm.licenseBack = null;
      this.addDriverForm.licenseFront = null;
      this.addDriverForm.acBack = null;
      this.addDriverForm.acFront = null;
      this.addDriverForm.photo = null;
    }
  },
  mounted() {
    if(this.isEditDriver){
      const url = `driver/view?driverId=${this.getDriverId}`;
      fetch(url).then(response => response.json())
          .then(res => {

                this.addDriverForm.name = res.driverFisrtName;
                this.addDriverForm.dob = res.dob;
                this.addDriverForm.phoneNumber = res.phoneNo;
                this.addDriverForm.emp_id = res.emp;
                this.addDriverForm.dateOfJoining = res.date_of_joining;
                this.addDriverForm.gender = res.gender;
                this.addDriverForm.address = res.address;
                this.addDriverForm.bloodGroup = res.blood_group;
                this.addDriverForm.emergencyNumber = res.emergency_number;

          },
          ()=>{
            ElMessage({
              type: 'error',
              message: 'No driver found. Redirecting to drivers page',
            });
            setTimeout(() => {
              window.location.href = '#/drivers';
            }, 1000);

      });
    }
  }
}
</script>

