<template>
  <div style="margin-left: 10px;width: 350px;">
  <el-input v-model="searchVal" clearable @input="$emit('search', searchVal, selectedSearch)">
    <template #prepend>
      <el-popover
          v-model:visible="searchPopover"
          trigger="click"
          placement="bottom"
          :width="150"
      >
        <div>
          <template v-for="each in fields">
            <div v-if="isFieldSearchable(each)" class="search-fields" :class="each.VALUE === selectedSearch ? 'is-active' : ''" style="text-align: center;padding: 5px;font-size: 13px;" @click="searchSelected(each.VALUE)">
              {{ each.NAME }}
            </div>
          </template>
        </div>
        <template #reference>
          <el-button
              type="default"
              ref="SearchListBtn"
              size="small"
          >
            <span class="fa-stack text-light">
                  <el-icon :size="12">
                    <Search />
                  </el-icon>
                    <el-icon :size="8" style="margin-left: 5px;">
                      <CaretBottom />
                    </el-icon>
                  </span>
          </el-button>
        </template>
      </el-popover>
    </template>
  </el-input>
  </div>
</template>

<script>
import { Search, CaretBottom } from '@element-plus/icons-vue'
export default {
  name: "SimpleSearch",
  components: { Search, CaretBottom },
  props: {
    fields: { type: Array, default: () => [] },
    defaultSearch: { type: String, default: null }
  },
  data(){
    return{
      searchVal: "",
      searchPopover: false,
      selectedSearch: ""
    }
  },
  computed: {

  },
  methods: {
    searchSelected(val){
      this.selectedSearch = val;
      this.searchPopover = false;
    },
    isFieldSearchable(field){
      return (field.hasOwnProperty("search") ? field.search : true);
    }
  },
  mounted() {
    if(this.defaultSearch){
      this.selectedSearch = this.defaultSearch;
    }
  }
}
</script>

<style lang="css">
.search-fields:hover{
  background: #f8f8f8;
  color: #1e1e1e;
  cursor: pointer;
}

.search-fields.is-active{
  color: #009bf5 !important;
  background: #f8f8f8 !important;
}

</style>