<template>
  <div class="list-table">
  <el-table @row-click="(row) =>$emit('rowClick', row)" @selection-change="handleSelectionChange" :data="tableData" style="width: 100%; height: 100%;" :class="clazz" :row-class-name="rowClazz">
    <el-table-column v-if="selectable" type="selection" width="55" />
    <el-table-column v-if="showIndex" type="index" label="#" width="50" />
    <template v-for="field in fields">
      <el-table-column :label="field.NAME" :sortable="isFieldSortable(field)" :width="field.width">
        <template #default="scope">
          <div style="display: flex;">
            {{ field.displayFunction ? field.displayFunction(scope.row) : displayValue(scope.row[field.VALUE]) }}
          </div>
        </template>
      </el-table-column>
    </template>
    <el-table-column fixed="right"  width="120" v-if="editable || viewable">
      <template #default="scope">
        <el-button v-if="viewable" type="info" size="small" :icon="getView" @click="$emit('view', scope.row)"></el-button>
        <el-button v-if="editable" type="info" size="small" :icon="getEdit" @click="$emit('edit', scope.row)"></el-button>
      </template>
    </el-table-column>
  </el-table>
  </div>
</template>

<script>
import {Edit, View} from "@element-plus/icons-vue";

export default {
  name: "ListTable",
  props: {
    tableData: { type: Array, default: () => [] },
    fields: { type: Array, default: () => [] },
    clazz: { type: String, default: ""},
    rowClazz: { type: Function, default: () => "" },

    editable: { type: Boolean, default: false },
    viewable: { type: Boolean, default: false },
    selectable: { type: Boolean, default: false },
    showIndex: { type: Boolean, default: false },
    sortable: { type: Boolean, default: false }

  },
  emits: {
    multiselect: null,
    rowClick: null
  },
  data(){
    return{

    }
  },
  computed: {
    getView(){
      return View;
    },
    getEdit(){
      return Edit;
    }
  },
  methods: {
    displayValue(val){
      if(val === undefined || !val) return "-";
      return val;
    },
    isFieldSortable(field){
      return this.sortable && (field.hasOwnProperty("sort") ? field.sort : true);
    },
    handleSelectionChange(selected){
      if(typeof selected === undefined) return;
      this.$emit("multiselect", selected);
    }
  }
}
</script>

<style lang="css">
.el-table .el-table__header tr th{
  background-color: #ececec !important;
  color: #1e1e1e;
}
</style>