<template>
  <div style="padding: 20px 20px;width: calc(100% - 40px);height: calc(100% - 130px); display: flex; flex-direction: column; padding-top: 5px !important;">
    <div class="table-bar" style="padding-top: 5px; padding-bottom: 5px;">
      <simple-search :fields="fields" default-search="DRIVER_NAME" @search="setSearch"></simple-search>
      <div class="block">
        <span class="demonstration">Pickup time filter:</span>
        <el-date-picker
            style="margin-left: 10px;"
            size="default"
            v-model="value2"
            type="datetimerange"
            :shortcuts="shortcuts"
            range-separator="To"
            start-placeholder="Start date"
            end-placeholder="End date">
        </el-date-picker>
      </div>

      <div class="action-button-group">
        <el-button style="background-color: #1bdb8a;border: none;" type="success" @click="openExport()">Export</el-button>
      </div>
    </div>
    <div style="height: calc(100% - 60px);width: calc(100% - 10px);margin-top: 20px;padding: 5px;background: white;box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;display: flex;flex-direction: column;">
      <div style="display: flex;justify-content: space-between;margin-bottom: 10px;width: 100%;">
        <div style="font-weight: 400;left: 0;">
          <el-tag>Total rides : {{ p_total }}</el-tag>
          <el-tag style="margin-left: 20px;">Total distance: {{ stats.distance }} KM</el-tag>
          <el-tag style="margin-left: 20px;">Total duration: {{ stats.duration }} MIN</el-tag>
          <el-tag style="margin-left: 20px;">Total cash: {{ stats.cash }} INR</el-tag>
          <el-tag style="margin-left: 20px;">Total digital: {{ stats.digital }} INR</el-tag>
        </div>
      </div>
      <list-table :fields="fields"
                  @multiSelect="setMultiselect"
                  :table-data="restData"
                  selectable
                  sortable
                  show-index>
      </list-table>
    </div>
  </div>
</template>

<script>
import { ElMessage, ElMessageBox } from 'element-plus'
import { Plus, Edit, View } from '@element-plus/icons-vue'
import ListTable from "@/admin/packages/table/ListTable";
import CommonMixin from "@/admin/packages/common-mixin";
import SimpleSearch from "@/admin/packages/components/search/SimpleSearch";
export default {
  name: "ViewDrivers",
  components: {SimpleSearch, ListTable},
  mixins: [CommonMixin],
  data(){
    return {
        shortcuts: [
            {
              text: 'Today',
            },
            {
              text: 'Current month',
            }
        ],
      value2: "",
      stats: {
        cash: "0",
        duration: "0",
        distance: "0",
        digital: "0"
      },
      statusMap: {
        ONGOING: { displayName: "Ongoing", color: "#f9c700" },
        COMPLETED: { displayName: "Completed", color: "#016a4e" },
      },
    }
  },
  computed: {
    fields(){
      return [
        {
          NAME: "Driver Name",
          VALUE: "DRIVER_NAME",
          clazz: "hello",
          sort: true
        },
        {
          NAME: "Vehicle",
          VALUE: "VEHICLE",
          clazz: "hello",
          sort: false
        },
        {
          NAME: "Pickup Date",
          VALUE: "DATE",
          clazz: "hello",
          sort: true,
          width: "250px",
          displayFunction: (rowData) => { return rowData.DATE + " " + rowData.PICKUP_TIME + " UTC" }
        },
        {
          NAME: "Distance",
          VALUE: "DISTANCE",
          clazz: "hello",
          sort: false,
          search: false,
          displayFunction: (rowData) => { return (rowData.DISTANCE || 0) + " Km" }
        },
        {
          NAME: "Duration",
          VALUE: "DURATION",
          clazz: "hello",
          sort: true,
          search: false,
          displayFunction: (rowData) => { return (rowData.DURATION || 0) + " Min" }
        },
        {
          NAME: "Cash",
          VALUE: "CASH",
          clazz: "hello",
          sort: false,
          search: false,
          displayFunction: (rowData) => { return (rowData.CASH || 0) + " INR" }
        },
        {
          NAME: "Digital",
          VALUE: "DIGITAL",
          clazz: "hello",
          sort: false,
          search: false,
          displayFunction: (rowData) => { return (rowData.DIGITAL || 0) + " INR" }
        }
      ]
    },
    p_total(){
      return this.restData.length;
    },
    getPlusIcon(){
      return Plus;
    },
    getEditIcon(){
      return Edit;
    },
    getViewIcon(){
      return View;
    }
  },
  methods: {
    openExport(){
      window.open("/export/csv", '_self');
    },
    getRideStats(){
      const url = "/stats/rides";
      fetch(url).then(response => response.json())
          .then(res =>{
            let obj = res.DATA[0];
            this.stats.distance = obj.TOTAL_DISTANCE.toFixed(2);
            this.stats.duration = obj.TOTAL_DURATION;
            this.stats.cash = obj.TOTAL_CASH.toFixed(2);
            this.stats.digital = obj.TOTAL_DIGITAL.toFixed(2);
          });
    },
    getUTC(time){
      let dateIST = new Date(time);
      dateIST.setHours(dateIST.getHours() + 5);
      dateIST.setMinutes(dateIST.getMinutes() + 30);
      return dateIST;
    },
    getTableData(){
      const url = "/rides";
      fetch(url).then(response => response.json())
          .then(res => this.rows = res.DATA);
    },
    deleteFunc(){
      if(this.multiSelectArray.length !== 0){
        ElMessageBox.confirm(
            'Are you sure want to delete?',
            'Warning',
            {
              confirmButtonText: 'OK',
              cancelButtonText: 'Cancel',
              type: 'warning',
            }
        ).then(() => {
          ElMessage({
            type: 'success',
            message: 'Delete completed',
          })
        });
      }
      else{
        ElMessage({
          type: 'info',
          message: 'Please select rows to delete',
        })
      }
    },
    exportFunc(){
      ElMessage({
        type: 'success',
        message: 'Exported as CSV',
      })
    }
  },
  mounted() {
    this.getRideStats()
    this.getTableData();
  }
}
</script>

